import { Education } from "../data";
export default function EducationComponent(props) {
  return (
    <div style={{display:'flex', flexDirection:'column', textAlign:'center',justifyContent:'center',  width: "100%", height: "350px", background: "blanchedalmond" }}>
      <h2>{props.primarySchool}</h2>
      <h2>{props.primaryYear}</h2>
      <h2> {props.highSchool}</h2>
      <h2>{props.highYear}</h2>

      <h2>{props.university}</h2>

      <h2> {props.universityYear}</h2>
    </div>
  );
}
