import React from 'react'
import "bootstrap/dist/css/bootstrap.min.css";
import './experience.css'

export default function ExperienceComponent({company,description,year,position}) {
  return (
    <div className='wrapper-experience'>
      <h1 className='companyh1'>{company}</h1>
      <h2>{year}</h2>
      <h2>{position}</h2>
      <p>{description}</p>


     
     
    </div>
  )
}
